export default {
  props: {
    remove: {
      type: Function,
      required: true
    }
  },
  data: () => ({
    rm_dialog  : false,
    target_rm  : null,
    list_items : [],
    expansion  : []
  }),
  computed: {
    customized_values() {
      return this.$store.state.customized_values
    },
    cart() {
      return this.$store.state.cart
    }
  },
  watch: {
    customized_values(newval, oldval) {
      const self = this
      if (newval) {
        // const paylaod = {
        //   store_name: this.site.store,
        //   source    : this.site.source,
        //   method    : "set",
        //   uuid      : this.site.uuid,
        //   detail    : {
        //     type : "pkg-order",
        //     items: [
        //       {
        //         delivery_date: this.min_rp,
        //         delivery_time: this.delivery_time_normal,
        //         items: newval
        //       }
        //     ]
        //   }
        // }
        // API.manage_cart(self.$store, paylaod)
        this.expansion = newval.map(el => ({ show: false }))
      }
    }
  },
  mounted() {
    if (!this.cart.length) {
      this.$router.go(-1)
    } else {
      this.cart.forEach(el => {
        this.list_items.push({
          id             : el.id,
          name           : el.name,
          SKU            : el.SKU,
          qty            : 0,
          image          : el.detail[0].main_image,
          variant        : el.detail[0].variant,
          normal_price   : el.detail[0].normal_price,
          discount_price : el.detail[0].discount_price
        })
        this.expansion.push({ show: false })
      })
    }
  },
}
